/*Range*/

.box {
  margin: 12px 5px;
  background: #fff;
  padding: 0px 4px;
  border-radius: 5px;
  box-shadow: 0px 0px 5px rgba(0, 0, 0, 0.1);
  display: flex;
  align-items: center;
  width: 100%;
  max-width: 250px;
}


.box .range {
  height: 40px;
  width: 100%;
  max-width: 300px;
  display: flex;
  align-items: center;
  margin-right: 15px;
}

.box .range input {
  height: 10px;
  width: 100%;
  -webkit-appearance: none;
  outline: none;
  background: #f2f2f2;
  border-radius: 25px;
  box-shadow: inset 0px 0px 4px rgba(0, 0, 0, 0.2);
  padding: 5px !important;
}

/* .range input::-moz-range-progress {
    height: 10px;
    border-radius: 25px;
    background: #57e;
} */

.range input::-webkit-slider-progress {
  -webkit-appearance: none;
  appearance: none;
  height: 10px;
  border-radius: 25px;
  background: var(--primary-color);
}

/* .range input::-moz-slider-thumb {
    height: 20px;
    width: 20px;
    border-radius: 50%;
    border: 4px solid #39d;
    background: white;
} */

.range input::-webkit-slider-thumb {
  -webkit-appearance: none;
  appearance: none;
  height: 20px;
  width: 20px;
  border-radius: 50%;
  border: 4px solid var(--primary-color);
  background: white;
}

.box .value {
  font-size: 1.2rem;
  /*font-weight: 600;*/
  font-family: sans-serif;
  color: var(--primary-color);
  width: auto;
  margin-right: 10px;
  text-align: center;
}


.filter-selectBox{
    border: 1px solid #ccc;
    flex: 1 1;
    width: 100%;
    min-width: 100px;
    color: var(--primary-color);
    background: transparent !important;
    padding: 6px;
}


.message-card.active{
  border: 1px solid var(--primary-color);
  box-shadow: 0px 0px 4px var(--primary-color);
}

.brand-card.active{
 border: 1px solid var(--primary-color);
  box-shadow: 0px 0px 4px var(--primary-color); 
